.contents {
  max-width: 1200px;
  margin: 100px auto;
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 24px;
}

.contents .form {
  flex-basis: 60%;
  height: 300px;
  border: 1px solid #ccc;
  padding: 16px;
}

.contents .list {
  flex-basis: 40%;
  height: 300px;
  border: 1px solid #ccc;
  padding: 16px;
}

.list_i {
  height: 280px;
  overflow-y: scroll;
  overflow-x: hidden;
}

.item {
  display: flex;
  align-items: center;
  gap: 0 24px;
  margin: 16px 0;
}